.newsletter{
    width: 65%;
    height: fit-content;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: auto;
    padding: 0px 140px;
    margin-top: 30px;
    margin-bottom: 150px;
    background: linear-gradient(rgb(213, 255, 216) 0%, rgb(212, 159, 238) 90% 100%);
    gap: 30px;
}
.newsletter h1{
    color: black;
    font-size: 55px;
    font-weight: 600;

}
.newsletter p{
    color: black;
    font-size: 20px;

}
.newsletter div{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
    background: whitesmoke;
    width: 730px;
    height: 70px;
    border-radius: 80px;
    border: 1px solid seagreen;
}
.newsletter input{
    width: 500px;
    margin-left: 30px;
    border: none;
    outline: none;
    color: bisque;
    font-family: poppins;
    font-size: 16px;
}
.newsletter div button{
    width: 210px;
    height: 70px;
    border-radius: 80px;
    background: black;
    color: whitesmoke;
    font-size: 16px;
    cursor: pointer;
}
@media(max-width:1280px){
    .newsletter{
        padding: 0px 80px;
        height: fit-content;
        margin-bottom: 80px;
        gap: 20px;

    }
    .newsletter h1{
        font-size: 36px;

    }
    .newsletter p{
        font-size: 16px;
    }
    .newsletter div{
        width: 600px;
        height: 60px;
    }
    .newsletter div input{
        width: 400px;
    }
    .newsletter div button{
        width: 160px;
        height: 60px;
    }
}
@media(max-width:1024px){
    .newsletter{
        height: fit-content;
        margin-bottom: 60px;
        gap: 20px;

    }
    .newsletter h1{
        font-size: 30px;

    }
    .newsletter p{
        font-size: 14px;
    }
    .newsletter div{
        width: 500px;
        height: 50px;
    }
    .newsletter div input{
        width: 300px;
        font-size: 14px;
    }
    .newsletter div button{
        width: 120px;
        height: 50px;
        font-size: 14px;
    }
}
@media(max-width:800px){
    .newsletter{
        height: fit-content;
        margin-bottom: 30px;

    }
    .newsletter h1{
        font-size: 18px;

    }
    .newsletter p{
        font-size: 12px;
    }
    .newsletter div{
        width: 400px;
        height: 40px;
    }
    .newsletter div input{
        width: 200px;
        font-size: 13px;
    }
    .newsletter div button{
        width: 100px;
        height: 40px;
        font-size: 13px;
    }
}
@media(max-width:500px){
    .newsletter{
        height: fit-content;
        width: 100%;
        padding: 0;
        gap: 15px;

    }
    .newsletter h1{
        font-size: 16px;

    }
    .newsletter p{
        font-size: 13px;
        max-width: 200px;
        text-align: center;
    }
    .newsletter div{
        width: 290px;
        
    }
    .newsletter div input{
        width: 130px;
        font-size: 13px;
    }
   
}
